<template>
<!-- Begin Electron desktop Capture modal -->
<div id="electron-desktop-capture-modal" class="modal">
    <div class="modal-content">
      <h4>Select Screen/Window <i class="fa fa-desktop"></i></h4>
      <br />

    <div style="height: 265px; !important; overflow: auto;">
        <p v-for="streamSource in streamSources" :key="streamSource.toString()" :id="streamSource.toString()">
      <label>
        <input v-model="selectedStream" class="with-gap" name="source" type="radio" :value="streamSource.id"  />
        <span class="bold">{{ streamSource.name }}</span>
      </label>
    </p>
    </div>

    </div>
    <div class="modal-footer">
        <button class="btn-small red waves-effect waves-light modal-close">CLOSE</button>
        <button @click="resolve" class="btn-small teal modal-close waves-effect waves-light">DONE</button>
    </div>
</div>
<!-- End Electron desktop Capture modal -->
</template>

<script lang="ts">
export default {
    name: 'electron-desktop-capture-modal',
    data(){
        return {
            streamSources: [] as any[],
            selectedStream: '',
            mounted: false as true|false
        }
    },
    //props: ['streamSources'],
    beforeCreate(){
      const $this = this;
        if((window as any).isElectron){
            (window as any).electron.desktopCapturer.getSources({types: ['screen', 'window']}).then((sources: any[]) => {
            ($this as any).streamSources = sources;
            ($this as any).mounted = true;
        }).catch(console.log);
        }
    },
    methods: {
        resolve(): any{
            const selected = (this as any).selectedStream;
            return (window as any).navigator.mediaDevices.getUserMedia({
          audio: false,
          video: {
            mandatory: {
              chromeMediaSource: 'desktop',
              chromeMediaSourceId: selected,
              minWidth: 1280,
              maxWidth: 1280,
              minHeight: 720,
              maxHeight: 720
            }
          }
        }).then((stream: any) => {
          (window as any).$store.commit('addInputSource', {
                                name: 'Live Desktop Capture (Video)',
                                id: Math.random(),
                                type: 'live desktop capture (video)',
                                data: stream,
                                position: 0
                            });
                            return setTimeout((window as any).focus, 1000);
                          }).catch(() => (window as any).M.toast({
                                    html: `Unable to get desktop capture on your device`,
                                    classes: 'bold red rounded'
                                }))
        }
    }
}
</script>

<style>
.modal, .modal-content{
    overflow: hidden !important;
}
</style>